import { createWebHistory, createRouter } from "vue-router";

import Home from '@/views/IndexPage.vue';
import Movies from '@/views/MoviesPage.vue';
import Songs from '@/views/SongPage.vue';


const routes = [
    {
        path:'/',
        name:'Home',
        component:Home
    },
    {
        path:'/movies',
        name:'Movies',
        component: Movies
    },
    {
        path:'/Songs',
        name:'Songs',
        component: Songs
    }
];

const router = createRouter({
    history : createWebHistory(),
    routes
})

export default router;